import React,{useState, useEffect} from "react";
import {Link} from "react-router-dom"
import { toast } from "react-toastify";
import fireDb from "../firebase";
const Productbill=() =>{
    const [data,setData]=useState({});
    useEffect(()=>{
        fireDb.child("foodies").on("value", (snapshot)=>{
            if(snapshot.val()!=null){
               setData({...snapshot.val()});
            }else{
               setData({});
            }
        })
        return()=>{
            setData({});
        }
    },[])
  const onAdding=(id)=>{
    window.confirm(id);
  }
    const onDelete=(id)=>{
       // console.log("Inside delete");
        if(window.confirm("Do You want to Delete this Product")){
            fireDb.child(`foodies/${id}`).remove((err)=>{
                if(err){
                    toast.error(err);
                }else{
                    toast.success("Deleted Successfully");
                }
            });
        }
    }
    return(
        <div>
          <table>
            <thead>
            <th>Name</th>
            <th>Price</th>
            <th>Quantity</th>
            <th>Action</th>
            </thead>
            <tbody>
                {Object.keys(data).map((id,index)=>{
                    return (
                        <tr>
                        <td>{data[id].name}</td>
                        <td>{data[id].Price}</td>
                        <td>{data[id].Quantity}</td>
                        <td>
                        <button type="button" onClick={()=>onAdding(id)}>Add</button>
                        </td>
                        </tr>

                    )
                })}
               
            </tbody>
            </table> 
        </div>
    )
}

export default Productbill