import React,{useState, useEffect} from "react";
import Select from 'react-select';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { toast } from "react-toastify";
import fireDb from "../firebase";
import "./home.css"
const Home=() =>{
    const options = [
        { value: 'Atul', label: 'Atul' },
        { value: 'Honey', label: 'Honey' },
        { value: 'Local', label: 'Local' },
      ];
      const [selectedOption, setSelectedOption] = useState(null);
    const [data,setData]=useState({});
    useEffect(()=>{
        fireDb.child("foodiedb").on("value", (snapshot)=>{
            if(snapshot.val()!=null){
               setData({...snapshot.val()});
            }else{
               setData({});
            }
        })
        return()=>{
            setData({});
        }
    },[])

    const onDelete=(id)=>{
  console.log(datetodayinddmmyyyy);
        if(window.confirm("Do You want to Delete this Product")){
            fireDb.child(`foodiedb/${id}`).remove((err)=>{
                if(err){
                    toast.error(err);
                }else{
                    toast.success("Deleted Successfully");
                }
            });
        }
    }
    const [startDate, setStartDate] = useState(null);
    let datetodayinddmmyyyy=null;
    if(startDate!=null){
        datetodayinddmmyyyy=startDate.getDate() + "-"+ parseInt(startDate.getMonth()+1) +"-"+startDate.getFullYear();
    }
    let totalamount=0;
    return(
        <div>
            <p style={{marginLeft:-50+'em'}}>
<DatePicker selected={startDate} onChange={(date) => setStartDate(date)} /></p>

<Select
        defaultValue={selectedOption}
        onChange={setSelectedOption}
        options={options}
      />
          <table style={{marginTop:3+'em'}}>
            <thead>
            <th>Name</th>
            <th>Price for one</th>
            <th>Quantity</th>
            <th>Total Price</th>
            <th>Date</th>
            <th>Action</th>
            </thead>
            <tbody>
                {Object.keys(data).map((idx,index)=>{
                    let datt;
                    if(datetodayinddmmyyyy==null && selectedOption==null){
                        datt=true;
                    }else if(datetodayinddmmyyyy!=null && selectedOption==null){
                    datt=(data[idx].date==datetodayinddmmyyyy);
                    }else if(datetodayinddmmyyyy==null && selectedOption!=null){
                      datt=(selectedOption.value==data[idx].num);

                    }else{
                        datt=(data[idx].date==datetodayinddmmyyyy) && (selectedOption.value==data[idx].num)
                    }
                    return (
                        
                        datt &&  
                        <tr>
                        <td>{data[idx].name}</td>                    
                        <td>{data[idx].price}</td>
                        <td>{data[idx].qty}</td>
                        <td>{data[idx].price * data[idx].qty}</td>
                        <td>{data[idx].date}</td>
                        <td><button type="button" onClick={()=>onDelete(idx)}>Delete</button>  </td>
                        <p style={{display:'none'}}>{totalamount+=(data[idx].price * data[idx].qty)}</p>
                        </tr>
                        
                    )
                })}
                </tbody>
            </table> 
               <h5>Total Amount is {totalamount}</h5>
            
        </div>
    )
}

export default Home