import React,{useState, useEffect} from "react";
import {useNavigate, useParams} from "react-router-dom"
import fireDb from "../firebase";
import {toast} from "react-toastify";
const initialState={
    name:"",
    Price:"",
    Quantity:""
}
const AddEdit=() =>{
    const [state,setState]=useState(initialState);
    const [data,setData]=useState({});
    const {name, Price, Quantity}=state;
    const navigate = useNavigate();
const {id}=useParams();
useEffect(()=>{
    fireDb.child("foodies").on("value", (snapshot)=>{
        if(snapshot.val()!=null){
           setData({...snapshot.val()});
        }else{
           setData({});
        }
    })
    return()=>{
        setData({});
    }
},[id]);

useEffect(()=>{
    if(id){
        setState({...data[id]})
    }else{
        setState({...initialState})
    }
    return()=>{
        setState({...initialState}) 
    }
},[id,data]);

    const handleSubmit=(e)=>{
        e.preventDefault();
        if(!name || !Price ||!Quantity){
            toast.error("Please Provide values");
        }else{
            if(!id){
            fireDb.child("foodies").push(state, (err)=>{
                if(err){
                 toast.error(err);
                }else{
                 toast.success("Date entered");
                }
            });
        }else{
            fireDb.child(`foodies/${id}`).set(state, (err)=>{
                if(err){
                 toast.error(err);
                }else{
                 toast.success("Date Updated");
                }
            });
        }
            setTimeout(()=> navigate("/"),500);
        }
    };
    const handleInputChange=(e)=>{
        const {name,value}=e.target;
        setState({...state,[name]:value})
    };
    return(
        <div>
        <form onSubmit={handleSubmit}>
        <label htmlfor="name">Name</label>
        <input type="text" id="name" name="name" value={name || ""} onChange={handleInputChange}></input>

        <label htmlfor="Price">Price</label>
        <input type="text" id="Price" name="Price" value={Price || ""} onChange={handleInputChange}></input>

        <label htmlfor="Quantity">Quantity</label>
        <input type="text" id="Quantity" name="Quantity" value={Quantity || ""} onChange={handleInputChange}></input>
        <input type="submit" value={id ? "Update" : "Save"}/>
        </form>
        </div>
    )
}

export default AddEdit